@import url('https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,500;1,300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Exo 2', sans-serif;
}

/*font-family: 'Exo 2', sans-serif;*/
.navbar {
  background: #000000; 
  background: -webkit-linear-gradient(
    to right,
    #ffdd00,
    #ffdf5f
  );
  background: linear-gradient(to right,  #ffdd00,
  #ffdf5f);
  font-family: 'Exo 2', sans-serif;
  font-size: 22px;
  line-height: 35px;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid gray !important;
}

.nav-link {
  font-weight: 300px !important;
}

.active{
  font-weight: 310px ;
}

.navbar-brand {
  font-weight: 600;
  letter-spacing: 4px;
  
}

.footer {
  padding: 25px;
  background: #000000; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #ffdd00,
    #ffdf5f
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right,  #ffdd00,
  #ffdf5f);
  font-family: 'Exo 2', sans-serif;
  
}

.footer a{
  font-family: 'Exo 2', sans-serif;
  font-size: 22px;
  text-decoration: none;
  color: #000000;
  padding: 12px;
  font-weight: 300px !important;

}

.footer a :hover{
  font-weight: 310px ;
}

/* =================================
======== page not found css ======= */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}

/* =========contact us ========= */
.contactus {
  margin: 0;
  padding: 0;
  min-height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}


.about {
  margin: 0;
  padding: 0;
  min-height: 140vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ===================================
========= register page  */
.form-container {
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50vh;
  flex-direction: column;
  background-color: #ffffff;
 
}

.form-container form {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 30px;
  background: -webkit-linear-gradient(
    to right,
    #ffdd00,
    #ffdf5f
  );
  background: linear-gradient(to right,  #ffdd00,
  #ffdf5f);
  border-radius: 10%;
}

.form-container form .title {
  text-align: center;
  margin-bottom: 15px;
  font-weight: bold;
  
}

.form-container form input {
  border: none;
  border-bottom: 1px solid #000;
  border-radius: 0;
  background: -webkit-linear-gradient(
    to right,
    #ffdd00,
    #ffdf5f
  );
  background: linear-gradient(to right,  #ffdd00,
  #ffdf5f);
}

.form-container form button {
  border: 1px solid black;
  border-radius: 0;
  background-color: #000;
  color: white;
  width: 250px;
}

.form-container form button:hover {
  border: 1px solid black;
  background: #000000; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #ffdd00,
    #ffdf5f
  );
  background: linear-gradient(to right,  #ffdd00,
  #ffdf5f);
   /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.forgot-btn {
  background-color: #434343a4 !important;
  border: 0 !important;
}

.forgot-btn:hover {
  color: black !important;
}

/* ================
====dashboard
=================== */

.dashboard {
  margin-top: 100px !important;
}

.dashboard-menu h4 {
  background: -webkit-linear-gradient(
    to right,
    #ffdd00,
    #ffdf5f
  );
  background: linear-gradient(to right,  #ffdd00,
  #ffdf5f);
  color: black;
  padding: 20px 0px;
}



.product-link {
  text-decoration: none !important;
  color: black !important;
}
